<template>
	<div class="list">
		<swiper :options="swiperOption" >
			<swiper-slide v-for="(item,index) in imgArr" :key='item.src'><img :src="item.src"/></swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
	</div>
</template>

<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	const imgArr = [{
		src:'/static/img/banner3.jpg'
	},{
		src:'/static/img/banner2.jpg'
	},{
		src:'/static/img/banner1.jpg'
	},{
		src:'/static/img/banner4.jpg'
	}];
	export default {
		name: 'swipeList',
		components: {
			swiper,
			swiperSlide
		},
    created() {
		  console.log('1111')
      console.log(imgArr)
    },
		data() {
			return {
				swiperOption: {
          slidesPerView: 4,
          spaceBetween: 10
				},
				imgArr: imgArr
			}
		}
  }
</script>

<style lang='scss'>
	.list {
		width:100%;
		left: 0;
		top: 790px;
		height: 222px;
    margin-bottom: -16px;
		img {
      height: 210px;
      left: 0;
			width: 100%;
    }
	}
</style>



