<template>
	<div class="swipe">
		<swiper :options="swiperOption" >
			<swiper-slide v-for="item in imgArr" :key='item.src'><img :src="item.src"/></swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
	</div>
</template>

<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
  const imgArr = [{
    src:'/static/img/banner3.jpg'
  },{
    src:'/static/img/banner2.jpg'
  },{
    src:'/static/img/banner1.jpg'
  },{
    src:'/static/img/banner4.jpg'
  }];
	export default {
		name: 'swipe',
		components: {
			swiper,
			swiperSlide
		},
		data() {
			return {
				swiperOption: {
					pagination: '.swiper-pagination',
					paginationClickable: true,
                    autoplay: 4000,
                    height: 911,
                    width: 1920
				},
				imgArr: imgArr
			}
		}
  }
</script>

<style>
	.swipe {
		left: 0;
		top: 0;
		width: 100%;
		margin: 0;
		padding: 0;
	}
    img {
		width:100%;
		height:100%;
        left: 0px;
    }
</style>



